* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}




.container {
    max-width: 1230px;
    padding: 0 15px;
    margin: 0 auto;
}

@mixin main-bg {
    background: #242528;
    color: #fff;
}

@mixin for-table {
    @media screen and (max-width:992px) {
        @content;
    }
}

@mixin for-mobile {

    @media screen and (max-width:568px) {
        @content;
    }

}


nav {
    @include main-bg();

    padding: 20px 0;
}

.navbar {
    display: flex;
    align-items: center;
    justify-content: space-around;



    &__menu {
        display: flex;
        align-items: center;
        list-style: none;
        text-transform: capitalize;
        gap: 20px;
        left: -100%;
        transition: 0.5s;



        @include for-mobile() {
            position: fixed;
            flex-direction: column;
            width: 100%;
            top: 90px;
            padding: 20px;
            padding-bottom: 50px;
            @include main-bg();
        }

        &-li {
            cursor: pointer;


            &-link {
                border: 1px solid #fff;
                border-radius: 100px;
                padding: 10px 15px;
                text-decoration: none;
                color: #fff;
            }
        }

    }

    .open-menu {
        display: none;
        border: none;
        background: local;

        @include for-mobile() {
            display: block;
        }
    }

    .color-replace {
        position: fixed;
        top: 3%;
        right: 4%;
        border: none;
        background: local;
        outline: none;
    }
}


.block {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    position: fixed;
    width: 100%;
    height: 100vh;
    padding: 20px;
    background: #000000b9;
    transform: scale(0);
    transition: 0.3s;
}


form {
    display: flex;
    flex-direction: column;
    row-gap: 20px;
    padding: 20px;

    @include main-bg();

    border: 1px solid #fff;
    border-radius: 10px;


}

h3 {
    text-transform: capitalize;
    color: #ffffffb5;
    cursor: pointer;

}

input,
button {
    padding: 7px;
    color: #ffffffb5;

    background: local;
    outline: none;
    border: 1px solid #cccccb9f;
    border-radius: 5px;
}

button {
    cursor: pointer;
}

.menu-for {
    left: 0;
}

.color-for {
    background: #0328fc;

}

.d-ok {
    transform: scale(1);
}